<template>
  <div class="home">
    <landing v-if="settings" :setting="settings" />
    <about v-if="settings" :setting="settings" />
    <skills />
    <portfolio />
    <contact />
  </div>
</template>

<script>
import About from "../components/About.vue";
import Skills from "../components/Skills.vue";
import Landing from "../components/Landing.vue";
import Contact from "../components/Contact.vue";
import Portfolio from "../components/Portfolio.vue";
// @ is an alias to /src

export default {
  name: "Home",
  components: { Landing, About, Skills, Contact, Portfolio },
  data() {
    return {
      settings: [],
    };
  },
  mounted() {
    this.getSettings();
  },
  methods: {
    async getSettings() {
      await this.axios.get("settings").then((data) => {
        this.settings = data.data[0];
      });
    },
  },
};
</script>
