    </h2>
    <div class="conatiner mx-auto" v-if="portfolios.length">
      <div class="image">
        <div
          class="img-container"
          v-for="portfolio in portfolios"
          :key="portfolio.id"
        >
          <portfolio-card :portfolio="portfolio" />
        </div>
