<script>
// import Modal from "./Modal.vue";
import PortfolioCard from "./PortfolioCard.vue";
export default {
  components: { PortfolioCard },
  data() {
    return {
      modalActive: null,
      portfolios: [],
      previweId: "",
    };
  },
  mounted() {
    this.getPortofolio();
  },
  methods: {
    closeModal() {
      this.modalActive = !this.modalActive;
    },
    previwe(id) {
      this.previweId = id.id;
      this.modalActive = !this.modalActive;
    },
    getPortofolio() {
      this.axios.get("portfolios").then(({ data }) => {
        this.portfolios = data;
      });
    },
  },
};
</script>

