<template>
  <section id="contact">
    <div class="container mx-auto p-11">
      <h2 class="text-center text-3xl font-bold">Get in touch</h2>

      <div class="wrapper mx-auto p-4">
        <validation-observer v-slot="{ invalid }" ref="contactForm">
          <div class="wrapper">
            <div class="flex flex-wrap justify-between mb-4">
              <validation-provider
                name="name"
                rules="required|min:3|max:80"
                v-slot="error"
                class="w-full mb-4 md:w-4/12"
              >
                <input
                  v-model="message.name"
                  type="text"
                  class="bg-third w-full px-5 py-2 rounded-3xl hover:outline-none"
                  placeholder="Name"
                />
                <div class="text-red-500 flex text-xs justify-center">
                  {{ error.errors[0] }}
                </div>
              </validation-provider>
              <validation-provider
                name="email"
                rules="required|min:3|max:150"
                v-slot="error"
                class="w-full md:w-4/12 mb-4"
              >
                <input
                  v-model="message.email"
                  type="email"
                  class="bg-third w-full px-5 py-2 rounded-3xl hover:outline-none"
                  placeholder="Email"
                />
                <div class="text-red-500 text-xs flex justify-center">
                  {{ error.errors[0] }}
                </div>
              </validation-provider>
              <validation-provider
                name="subject"
                rules="required|min:3|max:150"
                v-slot="error"
                class="w-full md:w-4/12 mb-4"
              >
                <input
                  v-model="message.subject"
                  type="text"
                  class="bg-third w-full px-5 py-2 rounded-3xl hover:outline-none"
                  placeholder="Subject"
                />
                <div class="text-red-500 text-xs flex justify-center">
                  {{ error.errors[0] }}
                </div>
              </validation-provider>
              <validation-provider
                name="message"
                v-slot="error"
                rules="required|min:10"
                class="w-full mb-4"
              >
                <textarea
                  class="resize-none rounded-2xl p-4 w-full bg-third border border-third text-white text-md"
                  name="message"
                  placeholder="Your Message"
                  v-model="message.message"
                  rows="4"
                ></textarea>

                <div class="text-red-500 text-xs flex justify-center">
                  {{ error.errors[0] }}
                </div>
              </validation-provider>
            </div>

            <div class="flex justify-center"></div>
          </div>
          <div class="flex justify-center">
            <button
              style="width: fit-content"
              class="rounded-xl mt-6 px-5 py-2 w-1/4"
              :class="invalid ? 'cursor-default bg-gray-500' : 'bg-primary'"
              :disabled="invalid || loading"
              :loading="loading"
              @click="send()"
            >
              <span> Send </span>
            </button>
          </div>
        </validation-observer>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      message: {
        name: "",
        email: "",
        subject: "",
        message: "",
      },
      loading: false,
    };
  },
  methods: {
    send() {
      this.loading = true;
      this.axios
        .post(
          "https://my-profile-8cc79-default-rtdb.firebaseio.com/messages.json",
          this.message
        )
        .then(() => {
          this.$toasted.show(`Your message sent successfull`, {
            type: "success",
            duration: 3000,
            position: "bottom-center",
          });
          this.$refs.contactForm.reset();
        })
        .catch((err) => {
          console.log(err);
        })
        .finally(() => {
          this.message.name = "";
          this.message.email = "";
          this.message.subject = "";
          this.message.message = "";
          this.loading = false;
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.wrapper {
  max-width: 1024px;
}
</style>
