<template>
  <div id="app" class="bg-secondary">
    <navbar />

    <router-view />
    <div class="go-top flex items-center justify-center" v-if="goTop">
      <a href="#" class="arrow-up">
        <font-awesome-icon
          class="text-primary"
          :icon="['fas', 'arrow-up']"
        ></font-awesome-icon>
      </a>
    </div>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar.vue";
export default {
  components: { Navbar },
  data() {
    return {
      goTop: null,
    };
  },
  created() {
    window.addEventListener("scroll", this.chechScroll);
  },
  methods: {
    chechScroll() {
      if (window.scrollY > 600) {
        this.goTop = true;
        return;
      }

      this.goTop = false;
      return;
    },
  },
};
</script>

<style lang="scss">
.go-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 40px;
  height: 40px;

  animation: bounce 2s linear infinite;
  @apply rounded-full border border-primary cursor-pointer;
}
input,
textarea {
  &:hover {
    outline: none;
  }
  &:focus {
    outline: none;
  }
}
.fadeInleft {
  animation-delay: 0.8s;
  animation: fadeInleft 1s forwards linear;
}
.fadeInright {
  animation-delay: 0.8s;
  animation: fadeInright 1s forwards linear;
}

@keyframes fadeInleft {
  0% {
    transform: translateX(-40px);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes fadeInright {
  0% {
    transform: translateX(40px);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes bounce {
  0%,
  20%,
  30%,
  40% {
    transform: translateX(10px);
  }
  60% {
    transform: translateX(0px);
  }
}
</style>
