<template class="">
  <nav class="p-4" :class="scroll ? 'scroll' : 'no-scroll'">
    <div class="container mx-auto flex justify-between">
      <div class="brand">
        <a href="#" class="font-bold">Ezzat Ellozy</a>
      </div>

      <div class="links hidden md:block">
        <a @click="mobileNav = !mobileNav" href="#">Home</a>
        <a @click="mobileNav = !mobileNav" href="#about">About</a>
        <a @click="mobileNav = !mobileNav" href="#skills">Skills</a>
        <a @click="mobileNav = !mobileNav" href="#Portfolio">Portfolio</a>
        <a @click="mobileNav = !mobileNav" href="#contact">Contact</a>
      </div>
      <button class="flat block md:hidden" @click="mobileNav = !mobileNav">
        <font-awesome-icon
          :icon="['fas', 'bars']"
          class="text-white text-2xl"
        ></font-awesome-icon>
      </button>
    </div>
    <transition name="navbar">
      <div
        class="menu-mobile"
        v-if="mobileNav"
        :class="mobileNav ? 'h-auto' : ''"
      >
        <div class="links flex flex-col">
          <a @click="mobileNav = !mobileNav" href="#">Home</a>
          <a @click="mobileNav = !mobileNav" href="#about">About</a>
          <a @click="mobileNav = !mobileNav" href="#skills">Skills</a>
          <a @click="mobileNav = !mobileNav" href="#Portfolio">Portfolio</a>
          <a @click="mobileNav = !mobileNav" href="#contact">Contact</a>
        </div>
      </div>
    </transition>
  </nav>
</template>

<script>
export default {
  data() {
    return {
      mobileNav: null,
      windownWidth: null,
      scroll: null,
    };
  },
  created() {
    window.addEventListener("resize", this.checkWidth);
    window.addEventListener("scroll", this.chechScroll);
    this.checkWidth();
  },

  methods: {
    chechScroll() {
      if (window.scrollY > 50) {
        this.scroll = true;
        return;
      }
      this.scroll = false;
      return;
    },
    checkWidth() {
      this.windownWidth = window.innerWidth;
      if (this.windownWidth > 768) {
        this.mobileNav = false;
        return;
      }
    },
  },
};
</script>

