<template>
  <section id="skills">
    <div class="container pt-20 px-5 myskills mx-auto">
      <h2 class="font-quickSand text-3xl text-center font-bold mb-11">
        My Skills
      </h2>
      <div class="skills">
        <div class="skill" v-for="skill in skills" :key="skill.id">
          <h3 class="flex justify-between">
            {{ skill.name }} <span>{{ skill.progress }}</span>
          </h3>
          <div class="progress">
            <span :data-width="skill.progress"></span>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      skills: null,
    };
  },
  created() {
    window.addEventListener("scroll", this.skillsProgressHandle);
  },
  mounted() {
    this.fetchSkills();
  },
  methods: {
    skillsProgressHandle() {
      let myskills = document.querySelector(".myskills");
      let spans = document.querySelectorAll(".progress span");

      if (window.scrollY >= myskills.offsetTop - 300) {
        // console.log("true");
        spans.forEach((span) => {
          span.style.width = span.dataset.width;
        });
      }
    },
    fetchSkills() {
      this.axios.get("myskills").then(({ data }) => {
        this.skills = data;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
  min-height: calc(100vh - 60px);
  max-width: 600px;
}
.skill {
  @apply mb-5;

  h3 {
    @apply text-lg font-bold font-quickSand mb-1;
    span {
      @apply border border-white rounded-lg p-1 text-center text-primary text-xs;
    }
  }

  .progress {
    @apply rounded-lg overflow-hidden;
    height: 30px;
    background-color: #eee;
    position: relative;
    span {
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      @apply bg-primary h-full;
      transition: width 0.5s linear;
    }
  }
}
</style>
